
import { defineComponent, reactive, ref, toRefs } from "vue";
import { successMessage } from "@/util/message";
import { useI18n } from "vue-i18n";

export default defineComponent({
    name: "PsersonCenter",
    setup() {
        const { t: $t } = useI18n();

        const loginTime = ref([
            {
                ip: "183.195.52.197,106.13.130.94",
                browser: "Chrome 85",
                system: "Windows 10",
                time: "2020-09-05 00:44:06",
            },
            {
                ip: "183.195.52.197,106.13.130.94",
                browser: "Chrome 85",
                system: "Windows 10",
                time: "2020-09-05 00:44:06",
            },
            {
                ip: "183.195.52.197,106.13.130.94",
                browser: "Chrome 85",
                system: "Windows 10",
                time: "2020-09-05 00:44:06",
            },
            {
                ip: "183.195.52.197,106.13.130.94",
                browser: "Chrome 85",
                system: "Windows 10",
                time: "2020-09-05 00:44:06",
            },
        ]);

        let states = reactive({
            name: "Super Admin",
            sex: 1,
            email: "yx17714503091@163.com",
            introduction: "又是充满希望的一天！",
        });

        let passWord = reactive({
            oldPass: "",
            newPass: "",
        });

        let updateLoading = ref(false);

        const passFormRef = ref();
        const rules = reactive({
            oldPass: [
                {
                    required: true,
                    message: $t("personCenter.editPwdInfo.requireError"),
                    trigger: "change",
                },
                {
                    validator: (rule: any, value: string, callback: any) => {
                        if (value.length < 6) {
                            callback(new Error($t("personCenter.editPwdInfo.less6Error")));
                        } else if (value !== "" && value !== passWord.newPass) {
                            callback(new Error($t("personCenter.editPwdInfo.differentError")));
                        } else {
                            passFormRef.value.clearValidate();
                            callback();
                        }
                    },
                    trigger: "change",
                },
            ],
            newPass: [
                {
                    required: true,
                    message: $t("personCenter.editPwdInfo.requireError"),
                    trigger: "change",
                },
                {
                    validator: (rule: any, value: string, callback: any) => {
                        if (value.length < 6) {
                            callback(new Error($t("personCenter.editPwdInfo.less6Error")));
                        } else if (value !== "" && value !== passWord.oldPass) {
                            callback(new Error($t("personCenter.editPwdInfo.differentError")));
                        } else {
                            passFormRef.value.clearValidate();
                            callback();
                        }
                    },
                    trigger: "change",
                },
            ],
        });
        const updatePass = () => {
            passFormRef.value.validate((valid: boolean) => {
                if (valid) {
                    updateLoading.value = true;
                    setTimeout(() => {
                        successMessage("Update Successfully");
                        passFormRef.value.resetFields();
                        updateLoading.value = false;
                    }, 1000);
                } else {
                    return false;
                }
            });
        };
        const reset = () => {
            passFormRef.value.resetFields();
        };
        return {
            loginTime,
            ...toRefs(states),
            passWord,
            passFormRef,
            reset,
            updatePass,
            updateLoading,
            rules,
        };
    },
});
